// ======================================================
// Abstracts :: Variables
// ======================================================

// Colors
$bg-body: #fefefe;
$border-color: #EDF0F2;

$black: #000;
$white: #fff;
$light: #fff;
$light-active: #fcfcfc;
$dark: #1e2129;
$dark-active: #111318;
$secondary: #f9f9f9;
$secondary-active: #f9f9f9;
$transparent: transparent !default;
$brand:    #ff6f1e !default;
$brand-active:    #f15700 !default;
$blue:    #1b84ff !default;
$blue-active:    #056ee9 !default;
$blue-light: rgba(#056ee9, 0.2) !default;
$green:   #17c653 !default;
$green-active:   #04b440 !default;
$red:     #f8285a !default;
$red-active:     #d81a48 !default;
$orange:  #f6b100 !default;
$orange-active:  #dfa000 !default;
$purple:  #7239ea !default;
$purple-active:  #5014d0 !default;

$indigo:  #6A00FF !default;
$pink:    #E62E7B !default;
$yellow:  #FFCB33 !default;
$teal:    #2EE6CA !default;
$cyan:    #66FFE8 !default;

$gray-100: #f9f9f9;
$gray-200: #f1f1f4;
$gray-300: #dbdfe9;
$gray-400: #c4cada;
$gray-500: #99a1b7;
$gray-600: #78829d;
$gray-700: #4b5675;
$gray-800: #252f4a;
$gray-900: #071437;

$default-box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, .09);
$light-box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, .03);
$blue-box-shadow: 0px 4px 12px 0px rgba($blue, .35);
$green-box-shadow: 0px 4px 12px 0px rgba($green, .35);
$red-box-shadow: 0px 4px 12px 0px rgba($red, .35);
$purple-box-shadow: 0px 4px 12px 0px rgba($purple, .35);
$orange-box-shadow: 0px 4px 12px 0px rgba($orange, .35);
$dark-box-shadow: 0px 4px 12px 0px rgba($dark, .35);

$card-box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, .03);
$tooltip-box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, .03);
$popover-box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, .03);
$modal-box-shadow: 0px 10px 14px 0px rgba(15, 42, 81, .03);
$drawer-box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, .03);
$dropdown-box-shadow: 0px 7px 18px 0px rgba(0, 0, 0, .09);
$input-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, .1);

$card-background-color: #fff;
$tooltip-background-color: #0f1014;
$popover-background-color: #fff;
$modal-background-color: #fff;
$drawer-background-color: #fff;
$dropdown-background-color: #fff;
$backdrop-background-color: rgba(0, 0, 0, .8);
$table-head-background-color: $light-active;

$card-border: 1px solid $gray-200;
$table-border: 1px solid $gray-200;
$dropdown-border: 1px solid $gray-200;
$popover-border: 1px solid $gray-200;
$tooltip-border: 0;

$header-height: 70px;
$zindex-1: 1000;
$zindex-2: 2000;
$zindex-3: 3000;
$zindex-4: 4000;
$zindex-5: 5000;
$zindex-6: 6000;
$zindex-7: 7000;
$zindex-8: 8000;
$zindex-9: 9000;


$list-colors: (
  "primary": $blue,
  "success": $green,
  "warning": $orange,
  "danger":  $red,
  "info":    $purple,
  "brand":   $brand,
  "indigo":  $indigo,
  "pink":    $pink,
  "yellow":  $yellow,
  "teal":    $teal,
  "cyan":    $cyan,
  "black":   $black,
) !default;

/*
* FONT
*/

.font-awesome{
  font-family: "Font Awesome 5 Pro";
}
