*, :after, :before {
    box-sizing: border-box;
    border-width: 0;
    border-style: solid;
    border-color: $gray-200;
}
ul{
    padding-left: 0;
    margin-bottom: 0;
    li{
        list-style: none; 
    } 
}

a{ text-decoration: none; }
.w-full { width: 100% }
.h-full { height: 100% }
.w-screen { width: 100vw }
.bg-cover{ background-size:cover; background-position: center; }
.border-transparent{ border-color:transparent!important; }
h1,h2,h3,h4,h5,h6{
    margin-bottom: 0;
}

/*
* Line Height
*/
@for $line-height from 0 through 100{
    .lh-#{$line-height}{
        line-height: #{$line-height}!important;
    }
}

/*
* FONT WEIGHT
*/
@for $font-weight from 1 through 9{
    .fw-#{$font-weight}{
        font-weight: #{$font-weight*100};
    }
}

/*
* WIDTH
*/
@for $width-px from 0 through 2000{
    @if $width-px <= 500 {
        .w-#{$width-px} {
            width: #{$width-px}px;
        }
    } @else if $width-px <= 1000 {
        @if $width-px % 25 == 0 {
            .w-#{$width-px} {
                width: #{$width-px}px;
            }
        }
    } @else {
        @if $width-px % 50 == 0 {
            .w-#{$width-px} {
                width: #{$width-px}px;
            }
        }
    }
}

/*
* HEIGHT
*/
@for $height-px from 0 through 2000{
    @if $height-px <= 500 {
        .h-#{$height-px} {
            height: #{$height-px}px;
        }
    } @else if $height-px <= 1000 {
        @if $height-px % 25 == 0 {
            .h-#{$height-px} {
                height: #{$height-px}px;
            }
        }
    } @else {
        @if $height-px % 50 == 0 {
            .h-#{$height-px} {
                height: #{$height-px}px;
            }
        }
    }
}

/*
* SIZE BOX
*/
@for $size-box from 1 through 500{
    .size-#{$size-box} {
        width: #{$size-box}px; 
        height: #{$size-box}px
    }
}

/*
* WIDTH PERCENT
*/
@for $width_percent from 1 through 20{
    .wp-#{$width_percent * 5} {
        width: #{$width_percent * 5}#{"%"};
    }
}

/*
* HEIGHT PERCENT
*/
@for $height-percent from 1 through 20{
    .hp-#{$height-percent * 5} {
        height: #{$height-percent * 5}#{"%"};
    }
}


/*
* MAX HEIGHT
*/
@for $max-height-rem from 0 through 20{
    .max-h-#{$max-height-rem}rem {
