// ======================================================
// Layout :: Header
// ======================================================

.header{
	position: fixed;
	top: 0;
	width: calc(100% - 70px);
	height: 70px;
	border-bottom: 1px solid $border-color;
	z-index: $zindex-8;
	background-color: $bg-body;
	margin-left: 70px;
} 
