.pagination{
	margin-bottom: 0px;
	display: flex;
	gap: 0.325rem;

	.page-item{

		.page-link{
			min-width: 28px;
			height: 28px;
			padding: 0;
			background-color: $light;
			color: $gray-700;
			font-weight: 500;
			font-size: .9125rem;
			border-radius: 6px;
			box-shadow: none;
			padding-left: 0.5rem;
			padding-right: 0.5rem;
			border: none;

			&:hover, &:focus{
				background-color: $gray-200;
			}

			&:before{
				@extend .font-awesome;
				position: relative;
				top: 1px;
			}

			&.first:before{
				content: "\f104";
			}

			&.last:before{
				content: "\f105";
			}

			&.next:before{
				content: "\f101";
			}

			&.previous:before{
				content: "\f100";
			}
		}

		&.active{
			.page-link{
				background-color: $gray-200;
				border: none;
				color: $gray-500;
			}
		}

		&.disabled{
			.page-link{
				color: $gray-500;
			}
		}
	}
}

