.sidebar{
	position: fixed;
	left: 0;
	top: 0;
	width: 280px;
	height: 100%;
	border-right: 1px solid $border-color;
	z-index: $zindex-9;
	background-color: $light;
	transition: all 0.3s;

	.sidebar-toggle{
		position: absolute;
		right: -12px;
	}
	
	*{
		text-wrap: nowrap;
	}

	.sidebar-header{
		height: $header-height;
	}

	.menu{
		height: calc(100% - 70px);
		padding-right: 0.5rem;
		overflow-x: hidden;

		.menu-item{

			.menu-link{
				color: $gray-800;
				padding: 0.5rem;
				display: flex;
				position: relative;
    			align-items: center;
				cursor: pointer;
				gap: 10px;

				&:after{
					display: none;
				}

				.menu-icon{
					width: 20px !important;
				    min-width: 20px;
				    text-align: center;
				}

				.menu-arrow{
					font-size: .6875rem;
				}

				&.collapsed{
					.menu-item-show{
						display: block;
					}

					.menu-item-hide{
						display: none;
					}
				}

				&:not(.collapsed){
					.menu-item-show{
						display: none;
					}

					.menu-item-hide{
						display: block;
					}
				}

				&:hover{
					color: $blue;
				}

				.menu-bullet{
					width: 5px;
					height: 5px;
					background-color: transparent;
					border-radius: 10px;
					position: absolute;
			        left: 16px;
    				top: 15px;
    				color: $gray-500;
				}
			
			}

			&.active{

				.menu-link{

					.menu-bullet{
						background-color: $blue!important;
					}

					.menu-title{
						color: $blue!important;
					}

				}
			}

			.menu-item{

				.menu-link{

					&:before{
						content: "";
					    width: 15px;
    					height: 8px;
						border-left: 1px solid $gray-300;
						border-bottom: 1px solid $gray-300;
						border-bottom-left-radius: 8px;
						margin-top: -6px;
						margin-left: -3px;
						min-width: 15px;
    					min-height: 8px;
					}

				}

				.menu-accordion{
					margin-left: 25px;
				}
				
			}

			.menu-accordion{
				margin-left: 10px;
				position: relative;

				&:before {
					left: 5px;
					height: calc(100% - 24px);
					content: "";
					position: absolute;
					border-left: 1px solid $gray-300;
				}
			}

			.menu-heading{
			    font-size: .8125rem;
				text-transform: uppercase;
				font-weight: 500;
				color: $gray-500;
				padding: 0 0.5rem;	
			}
		}

		.menu-top, .menu-bottom{
			padding-left: 1rem;
			padding-right: 1rem;
		}
		
	}

	&.small:not(:hover){
		width: 70px;

		.menu-item{
			.menu-link{
				.menu-title{
					display: none!important;
				}

				.menu-arrow{
					display: none!important;
				}
			}

			.menu-accordion{
				display: none;
			}
		}

		.menu-heading{
			text-align: center;

			&:before{
				content: "...";
			}

			span{
				display: none;
			}
		}
	}

		

	


}


