.fc{
	border-left: 0;

	.fc-header-toolbar{
		display: none;
	}

	.fc-scrollgrid{
		border:	0;
	}

	table{
		border: 0;

		colgroup{
			display: none;
		}

		tr > td.fc-day, tr > th.fc-day{
		    padding: .75rem 1rem;
			border-bottom: 1px solid $gray-200;
			border-top: 0;
			border-left: 0;
			border-right: 0;
			outline: none!important;
		}

		tr > th.fc-day{
			background-color: $gray-100;
			.dt-column-order{
				color: $gray-900;
			}

			a{
				color: $gray-700;
				font-weight: 700;
				font-size: .8125rem;
			}
		}

		tr > td.fc-day{

			a{
				width: 28px;
				height: 28px;
				display: flex;
				align-items: center;
				justify-content: center;
				border-radius: 100px;
				font-weight: 400;
				font-size: .8125rem;
				background-color: $gray-100;
				border: 1px solid $gray-200;
				color: $gray-500;
			}

			&.fc-day-today{
				background-color: $blue-100;
				a{
					background-color: $primary;
					color: $light;
				}
			}

			
		}

		tr > td.fc-day:not(:last-child), tr > th.fc-day:not(:last-child){
			border-right: 1px solid $gray-200;
		}
	}
}